.skeleton {
    background: #eee;
    background-image: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
    border-radius: 5px;
    background-size: 200% 100%;
    animation: 0.5s shine linear infinite;
}

@keyframes shine {
    to {
        background-position-x: -200%;
    }
}